import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { url } from '@sunl-fe/util';
import moment from 'moment';
import weixinShare from '../../wechat-tools/index';
import classNames from 'classnames';
import styles from '../../styles/less/post.less';
import { mr20 } from '../../styles/less/common.less';
import util from '../../common/util';
import { EMOTION_CN_TO_EN, EMOTION_EN_TO_CN } from '../../common/emotion';
import FixedTop from '../../components/fixedTop/Index';
import { envCfg } from '../../common/envCfg';
import GetMore from '../../components/getMore/Index';

import { GET_POST_DETAIL_REQUESTED } from '../../constants/post';

class Post extends React.Component {
    static defaultProps = {
        postMaster: {},
        resultList: []
    }

    constructor(props) {
        super(props);

        this.getPostDetail();
    }

    componentWillReceiveProps(nextProps) {
        let {postMaster} = nextProps;
        let {postSubject, content, externalLinks, postLinkList} = postMaster;

        const firstImageLink = postLinkList.filter(link => link.linkType.toUpperCase() === 'image')[0] || { linkUrl: '' }
        weixinShare({
            title: postSubject,
            desc: content,
            messageImgUrl: firstImageLink.linkUrl
        })
    }

    getPostDetail() {
        let { postMasterId } = this.props.params;
        if (postMasterId.indexOf('&') != -1) {
            postMasterId = postMasterId.split('&')[0];
        }
        let { dispatch } = this.props;
        dispatch({
            type: GET_POST_DETAIL_REQUESTED,
            params: {
                postMasterId,
                osVersion: 'PC',
                appVersion: '',
                pageNo: 1,
                pageSize: 10,
                userId: -1,
            }
        });
    }

    createAvatar(userId, isVip) {
        const vipClassName = (isVip === 1) ? classNames(styles.icon, styles['icon--vip']) : (
            isVip === 2 ? classNames(styles.icon, styles['icon--teacher']) : ''
        );

        return (
            <div>
                <img src={`http://static.sunlands.com/user_center/newUserImagePath/${userId}/${userId}.jpg`} className={styles.avatar} onError={this.handleAvatarError} />
                {
                    isVip ? (<i className={vipClassName}></i>): ''
                }
            </div>
        )
    }

    formatContent(content = '', postLinkList = []) {
        let emotionPlaceholderArr = [];
        let contentFormatted = content.replace(/\r\n/ig, '<br/>').replace(/\n/ig, '<br/>').replace(/\r/ig, '<br/>');

        //将[XX]转化为img
        for (let k in EMOTION_EN_TO_CN) {
            emotionPlaceholderArr.push('\\\[' + EMOTION_EN_TO_CN[k] + '\\\]');  //[爱慕]
        }

        let regExpStr = emotionPlaceholderArr.join('|');
        let regExp = new RegExp('(' + regExpStr + ')', 'g');  //全局匹配

        //将匹配到的表情占位符替换为img标签
        let rs;
        while(rs = regExp.exec(content)) {
            let imgName = rs[0].replace(/[\[\]]/g, '');
            contentFormatted = contentFormatted.replace(rs[0], `<img src="./images/emotion/${EMOTION_CN_TO_EN[imgName]}.png" class=${styles['emotion']}>`);
        }

        //图片
        for (let len = postLinkList.length, i = 0; i < len; i++) {
            const { linkType, linkUrl } = postLinkList[i]
            if (linkType.toLowerCase() === 'video') {
                contentFormatted += `<video src="${linkUrl}" class=${styles.video} controls>`;
            } else {
                contentFormatted += `<img src="${linkUrl}" class=${styles.img}>`;
            }
        }

        return {
            __html: contentFormatted
        }
    }

    formatTime(time) {
        var rs = '';
        if (!/\d{4}-\d{2}-\d{2}\s\d{2}:\d{2}:\d{2}/.test(time)) {
            return time;
        }
        var now = new Date();
        var nYear = now.getFullYear();
        var nMonth = now.getMonth() + 1;
        var nDay = now.getDate();

        var dayInfo = time.split(' ')[0];
        var timeInfo = time.split(' ')[1];
        var year = dayInfo.split('-')[0],
            month = dayInfo.split('-')[1],
            day = dayInfo.split('-')[2];
        if (year == nYear && month == nMonth) {
            //说明同年同月
            if (day == nDay) {
                //今天发的帖子
                rs = '今天' + moment(time).format('HH:mm');
                return rs;
            }
            if (day == nDay - 1) {
                //昨天发的帖子
                rs = '昨天' + moment(time).format('HH:mm');
                return rs;
            }
            return moment(time).format('MM-DD');
        }
        if (year == nYear) {
            //今年发的帖子
            return  moment(time).format('MM-DD');
        }

        return moment(time).format('YYYY-MM-DD');
    }

    handleAvatarError(e) {
        e.target.src="./images/default.png";
    }

    createPostSlaveList() {
        const { resultList } = this.props;
        let postSlaveList = resultList.map((item, index) => {
            const { userId, isVip, gradeCode, userNickname, createTime, content, postLinkList, replyDTOList } = item;
            const levelClassName = gradeCode < 6 ? styles['text--bluebg'] : (
                gradeCode < 11 ? styles['text--yellowbg'] : styles['text--redbg']
            );

            return (
                <div className={styles['post-slave']}>
                    <div className={styles['user-info-panel']}>
                        {this.createAvatar(userId, isVip)}
                        <div className={styles['info-panel--top']}>
                            {userNickname}
                            <span className={classNames(styles['text--lv'], levelClassName)}>Lv.{gradeCode}</span>
                        </div>
                        <div className={styles['info-panel--bottom']}>
                            第{index + 2}楼 {this.formatTime(createTime)}
                        </div>
                    </div>
                    <div className={styles.content} dangerouslySetInnerHTML={this.formatContent(content, postLinkList)}></div>
                    {
                        replyDTOList.length ?
                            <ul className={styles['reply-container']}>
                                {this.createReplyList(replyDTOList)}
                            </ul> : ''
                    }
                </div>
            )
        })

        return postSlaveList;
    }

    createReplyList(replyDTOList) {
        const {postMaster} = this.props;
        const masterUserId = postMaster.userId;

        replyDTOList.splice(3);

        let rs = replyDTOList.map(item => {
            const {userId, userNickname, replyToUserNickname, replyToReplyid, content} = item;
            return (
                <li className={styles['reply-item']}>
                    <span className={styles['user-name']}>{userNickname}</span>
                    {
                        userId === masterUserId ?
                        <span className={styles['text--pinkbg']}>楼主</span> : ''
                    }
                    {
                        replyToUserNickname &&
                            <span className={styles['user-name']}>
                                &nbsp;回复&nbsp;{replyToUserNickname}
                            </span>
                    }
                    {
                        replyToReplyid === masterUserId &&
                            <span className={styles['text--pinkbg']}>楼主</span>
                    }：
                    <span className={styles['text--gray']} dangerouslySetInnerHTML={this.formatContent(content)}></span>
                </li>
            )
        })

        return rs;
    }

    createGetMore() {
        const {resultList = []} = this.props;
        if (resultList.length == 10) {
            return (
                <GetMore paramName='postid' paramValue={this.props.params.postMasterId} />
            )
        } else {
            return null;
        }
    }

    render() {
        const {postMaster} = this.props;
        const {
            userId,
            gradeCode,
            isVip,
            userNickname,
            createTime,
            postSubject,
            content,
            modifyTime,
            praiseCount,
            replyCount,
            postLinkList,
        } = postMaster;

        const praiseClassName = classNames(styles.icon, styles['icon--praise']);
        const replyClassName = classNames(styles.icon, styles['icon--reply']);
        const levelClassName = gradeCode < 6 ? styles['text--bluebg'] : (
                gradeCode < 11 ? styles['text--yellowbg'] : styles['text--redbg']
            );

        return (
            <div>
                <FixedTop paramName='postid' paramValue={this.props.params.postMasterId}/>
                <div className={styles.container}>
                    <div className={styles['post-master']}>
                        <div className={styles['user-info-panel']}>
                            {this.createAvatar(userId, isVip)}
                            <div className={styles['info-panel--top']}>
                                {userNickname}
                                <span className={classNames(styles['text--lv'], levelClassName)}>Lv.{gradeCode}</span>
                            </div>
                            <div className={styles['info-panel--bottom']}>
                                {this.formatTime(createTime)}
                            </div>
                        </div>
                        {
                            postSubject && <div className={styles.title}>{postSubject}</div>
                        }
                        <div className={styles.content} dangerouslySetInnerHTML={this.formatContent(content, postLinkList)}></div>
                        <div className={styles['master-footer']}>
                            <div className={styles['footer--left']}>
                                更新于 {this.formatTime(modifyTime)}
                            </div>
                            <div className={styles['footer--right']}>
                                <i className={praiseClassName}></i>
                                <span className={styles['num--mr20']}>{praiseCount}</span>
                                <i className={replyClassName}></i>
                                <span className={styles.num}>{replyCount}</span>
                            </div>
                        </div>
                    </div>
                    {this.createPostSlaveList()}
                    {this.createGetMore()}
                </div>
            </div>
        )
    }
}

const getPost = (state) => {
    return state.post;
}
const selectors = createSelector(
    [getPost],
    (post) => {
        return {...post};
    }
)

export default connect(selectors)(Post);



