import { envCfg } from './common/envCfg'
import Main from './containers/Main'
import Home from './containers/home/Home'
import Notice from './containers/notice/Notice'
import Post from './containers/post/Post'
import Recording from './containers/recording/Recording'

const routes = [{
    path: '/',
    component: Main,
    indexRoute: { component: Home },
    childRoutes: [
        {
            path: 'topic/:topicName',
            component: Home,
        },
        {
            path: 'topic/:topicName/:topicId',
            component: Home,
        },
        {
            path: 'notice/:groupId',
            component: Notice,
        },
        {
            path: 'recording',
            component: Recording,
        },
        {
            path: 'post/:postMasterId',
            component: Post,
            onEnter: (nextState, replace) => {
                const hm = document.createElement('script')
                hm.src = envCfg.postStatisticsUrl
                const s = document.getElementsByTagName('script')[0]
                s.parentNode.insertBefore(hm, s)
                hm.onload = function () {
                    _hmt.push(['_setAutoPageview', false])
                    const hash = window.location.hash.split('?')[0]
                    _hmt.push(['_trackPageview', `/community-pc-war/m/index.html${hash}`])
                }
            },
        },
    ],
}]

export default routes


