import React from 'react';
import { appLinkDom } from '@sunl-fe/app-link-staff';
import styles from '../../styles/less/fixedTop.less';
import classNames from 'classnames';

class FixedTop extends React.Component {

    constructor(props) {
        super(props)
    }

    componentDidMount() {
        this.renderAppLinkDom();
    }

    componentDidUpdate() {
        this.renderAppLinkDom();
    }

    renderAppLinkDom() {
        if (this.refs.div && !this.refs.div.children.length) {
            if (this.props.paramGroup){
                this.refs.div.appendChild(appLinkDom(this.props.paramGroup));
            }else if (this.props.paramValue) {
                this.refs.div.appendChild(appLinkDom({
                    [this.props.paramName]: this.props.paramValue
                }));
            } else {
                this.refs.div.appendChild(appLinkDom());
            }
        }
    }

    // refresh = () => {
    //     location.reload();
    // }
    
    render() {
        return (
            <div className={styles['wrapper']} ref="div">
            </div>
        )
    }
}

export default FixedTop;



