import React from 'react'
import { gotoApp } from '@sunl-fe/app-link-staff'
import { Button } from 'antd-mobile'
import moment from "moment";
import weixinShare from '../../wechat-tools/index'
import FixedTop from '../../components/fixedTop/Index'
import styles from '../../styles/less/recording.less'

const record = require('../../images/recording/record.png')

class Recording extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }

    componentDidMount() {
        const {
            location: { query: { title = '', content = '', userImgurl = '', chineseName = '', userName = '' } }
        } = this.props
        weixinShare({
            title: decodeURIComponent(title),
            desc: decodeURIComponent(content),
            hideSharingMenu: true,
            imgUrl: decodeURIComponent(userImgurl),
        })
        document.title = chineseName ? decodeURIComponent(chineseName) : userName;
    }
    componentWillUnmount() {
        document.title = "尚德机构"
    }

    openApp = () => {
        const {
            location: { query: { callId = '', callTime = '', userName = '', opportunityId = '', chineseName = '', callDuration = '', } }
        } = this.props
        gotoApp({
            callId, callTime, userName, opportunityId, chineseName, callDuration
        });
    }

    render() {
        const {
            location: { query: { callId = '', callDuration = '', callTime = '', opportunityId = '', userName = '', chineseName = '', } }
        } = this.props
        const $time = moment("1970-01-01").add(callDuration, 'seconds')
        const duration = callDuration < 3600 ? $time.format('mm:ss') : $time.format('HH:mm:ss')
        return (
            <div>
                <FixedTop paramGroup={{ callId, callTime, userName, opportunityId, callDuration, chineseName }} />
                <div className={styles['content-wrapper']}>
                    <div className={styles.info}>
                        <div className={styles['info-text']}>机会ID：{opportunityId}</div>
                        <div className={styles['info-text']}>
                            <span className={styles['text']}>通话时间：{callTime ? moment(callTime).format('YYYY.MM.DD') : ''}</span>
                            <span className={styles['text']}>录音时长：{duration}</span>
                        </div>
                    </div>
                    <div className={styles.pic}>
                        <img className={styles.img} src={record} alt="" />
                    </div>
                    <div className={styles["listen-btn"]}>
                        <Button className={styles.button} onClick={this.openApp}>立即收听</Button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Recording


